import axios from "@/assets/api/axios.js";

const api = {
    getRoleList: (params) => axios("postjson", "/getRoleList", params), // 获取所有角色列表
    getInfo: (params) => axios("postjson", "/getRoleInfo", params), // 获取角色信息
    saveInfo: (params) => axios("postjson", "/postRoleInfo", params), // 保存角色信息
    saveXiulianStatus: (params) =>
        axios("postjson", "/postXiulianStatus", params), // 切换修炼状态
    postModifyRole: (params) => axios("postjson", "/postModifyRole", params), // 修改角色信息
};
export default api;
